<template>
    <div>
        <p>我是登录组件</p>
        <p>
            <button
                type="button"
                @click="loginIn">
                登录
            </button>
        </p>
    </div>
</template>

<script>
export default {
    name: 'VuexLogin',
    data() {
        return {};
    },
    methods: {
        loginIn() {
            const username = prompt('Enter Username');
            this.$store.dispatch('loginIn', {
                username,
                timestamp: Date.parse(Date()),
            });
        },
    },
};
</script>

