<template>
    <div page-index>
        <div class="cart"></div>
        <div>{{ $store.getters.formatName }}</div>
        <div>{{ fName }}</div>
        <div>{{ $store.state.pro }}</div>
        <div>{{ proName + '&ensp;' + proPrice }}</div>
        <div v-shadow="{ radius: 8 }">
            <p>
                <button
                    type="button"
                    @click="getLoginInfo">
                    点我获取登录用户信息
                </button>
            </p>
            <p>
                <strong>
                    登录信息:
                    <span>{{
                        $store.state.loginInfo.name
                            ? $store.state.loginInfo.name
                            : 'Username'
                    }}</span>
                    &ensp;/&ensp;
                    <span>{{
                        $store.state.loginInfo.timestamp
                            ? $store.state.loginInfo.timestamp
                            : ''
                    }}</span>
                </strong>
            </p>
            <p>
                <button
                    type="button"
                    @click="processName">
                    加工Username
                </button>
            </p>
            <hr />
            <VuexLoginVue />
        </div>
        <!-- <div v-card>
            <p>关于Vuex</p>
            <div>
                <pre>
是一个插件
数据状态管理\组件之间数据共享
                </pre>
            </div>
        </div> -->
        <NHCard title="关于Vuex">
            <pre>
是一个插件
数据状态管理\组件之间数据共享
            </pre>
        </NHCard>
    </div>
</template>

<script>
import VuexLoginVue from '@/common/VuexLogin.vue';
import { mapGetters, mapActions, mapState } from 'vuex';

export default {
    name: 'Vuex',
    components: { VuexLoginVue },
    data() {
        return {};
    },
    methods: {
        // getLoginInfo() {
        //     this.$store.dispatch('loginInfo');
        // },
        // processName() {
        //     this.$store.dispatch('processName');
        //     // console.log(this.$store.getters.formatName);
        // },
        ...mapActions({
            getLoginInfo: 'loginInfo',
            processName: 'processName',
        }),
    },
    computed: {
        /**
         * mapGetters将getters内的函数映射到计算属性(computed)中去
         * @author Naihe
         */
        ...mapGetters({
            fName: 'formatName',
        }),
        ...mapState('pro', ['proName', 'proPrice']),
    },
    mounted() {
        console.log(this);
    },
};
</script>

<style scoped>
.cart {
    position: fixed;
    right: 20rem;
    width: 400px;
    height: 255px;
    border-radius: 12px;
    background: url('D:/Sof/HBuilderX/Projects/Vue/vue/vue-cli/public/image/wall02.jpg')
        no-repeat;
    background-position: center center;
    background-size: auto 100%;
    transition: background 0.8s;
}

.cart:hover {
    background-size: auto 180%;
    background-position: left top;
    box-shadow: 0 0 30px 0 #999;
}
</style>
